body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cards {
    padding: 4rem;
    background: #fff;
  }
  
h1 {
    text-align: center;
  }
  
.cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
.cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
.cards__items {
    margin-bottom: 24px;
  }
  
.cards__item {
    display: flex;
    flex: 1 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
.cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
.cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
.fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }
  
.cards__item__pic-wrap::after {
    content: attr(data-category);
    position: left;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    box-sizing: border-box;
  }
  
.cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
.cards__item__img:hover {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  
.cards__item__info {
    padding: 20px 30px 30px;
  }
  
.cards__item__text {
    color: #252e48;
    font-size: 20px;
    line-height: 25px;
    text-align: center;
  }

.column {
    float: left;
    width: 33.33%;
    padding: 5px;
    box-sizing: border-box;
  }
  

.row::after {
    content: "";
    clear: both;
    display: table;
    box-sizing: border-box;
  }
  
@media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
@media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
@media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }

  @media only screen and (max-width: 800px) {
    .column {
      width: 100%;
      box-sizing: border-box;
    }
  }
